@use "../variables.scss";

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;

  @media (min-width: variables.$tablet-width) {
    display: flex;
    flex-wrap: wrap;
  }
}

.advantages__item {
  width: 100%;
  box-sizing: border-box;
}

.advantages__img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;

